.settings
{
    display: flex;
    font-family: 'Varela Round', sans-serif;
}

.settingsWrapper
{
    flex: 6;
    padding: 20px;
}

.settingsTitle
{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.settingsUpdateTitle
{
    font-size: 30px;
    margin-bottom: 20px;
    color: lightcoral;
}

.settingsDeleteTitle
{
    color: red;
    font-size: 15px;
    cursor: pointer;
}

.settingsForm
{
    display: flex;
    flex-direction: column;
}

.settingsPP
{
    display: flex;
    align-items: center;
    margin: 10px 0;
}

.settingsPP > img
{
    width: 75px;
    height: 75px;
    border-radius: 50%;
    object-fit: cover;
}

.settingsPPIcon
{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: lightcoral;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 45px;
    cursor: pointer;
}

.settingsForm >label
{
    font-size: 20px;
    margin-top: 20px;
}

.settingsForm > input
{
    color: gray;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgray;
}

.settingsSubmit
{
    width: 150px;
    align-self: center;
    border: none;
    border-radius: 10px;
    color: white;
    background-color: teal;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
}

