.post
{
    width: 400px;
    margin: 0px 25px 40px 25px;
}

.postImg
{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 7px;
}

.postInfo
{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.postCat
{
    font-family: 'Varela Round', sans-serif;
    font-size: 15px;
    color: #be9656;
    line-height: 20px;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
}

.postTitle
{
    font-family: 'Josefin Sans', sans-serif;
    font-size: 25px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.postDate
{
    font-family: 'Lora', serif;
    font-size: 13px;
    font-style: italic;
    margin-top: 15px;
    color: #999;
}

.postDesc
{
    font-family: 'Varela Round', sans-serif;
    font-size: 14px;
    color: #444;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical; 
}